* {
  font-family: "Mali", cursive;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-repeat: no-repeat;
  background-size: cover;
  background-image: linear-gradient(
    60deg,
    rgb(153, 88, 88),
    rgb(208, 166, 106)
  );
  min-height: 100vh;
}

/* forms */

.createForm {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 7vh;
}

.edit-post{
  margin-top: 7vh;
}

/* posts */

.posts-container {
  margin: 4rem 0rem 3rem;
}

.post {
  background-color: #d1c5b6;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
  padding: 5% 10% 10%;
}

.post .content {
  margin-top: 5%;
  font-size: 28px;
  white-space: pre-wrap;
}

.title {
  text-align: center;
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 80px;
}

.card {
  transition: 0.5s;
}



.card:hover {
  filter: opacity(0.95);
}


/* Perfil */


.perfil {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}

.section1 {
  flex-grow: 1;
  margin: 5%;
}

.section1 .img-container {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto;
  border: 5px solid #d1c5b6c9;
  display: flex;
  justify-content: center;
  box-shadow: 0px 2px 30px rgba(20, 19, 19, 0.923);
  background-color: #a45e4fb0;
}

.section1 .img-container img {
  height: 102%;
  object-fit: cover;
}

.section1 .info-container {
  margin-top: 1.5%;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  justify-items: center;
}

.section1 .info-container .username {
  font-weight: 700;
  font-size: 50px;
  margin-bottom: 10px;
  letter-spacing: 0.05em;
}



.section1 .info-container .description {
  box-shadow: 0 0 7px rgba(31, 30, 33, 0.705);
  padding: 2.5rem 2rem ;
  border-radius: 25px;
  background-color: rgba(26, 37, 62, 0.897);
  color: rgba(184, 162, 162, 0.924);
  margin-top: 1.5%;
  width: 45vw;
  cursor: pointer;
  transition: .3s;
  font-size: 1.8rem;
  border: none;
  justify-self: center;
  align-items: center;
  text-align: center;
}

.section1 .info-container .description:hover {
  filter: brightness(1.05);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.894);
}

.section1 .info-container .description:focus {
  border: 4px solid rgba(17, 5, 60, 0.674);
  filter: brightness(1.1);
  outline: none;

}


.section2 {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.664);
  flex-grow: 0;
  background-color: rgb(20, 24, 27);
  height: 90.8vh;
  width: 20vw;
  justify-self: end;
  transition: 0.5s;
  overflow-y: scroll;
}

.section2 .anecdotes-lenght {
  color: #ddd4cad2;
  font-size: 30px;
  letter-spacing: 0.05em;
  font-weight: 700;
  margin-top: 10%;
  margin-left: 5%;
}

.section2 .post-list {
  margin: 15% 4%;
  color: #ded4c7c0;
  font-size: 20px;
  letter-spacing: 0.05em;
  font-weight: 700;
  transition: 0.3s;
}

.section2 .post-list:hover {
  filter: brightness(2);
}

.section2 .mas {
  color: #d1c5b6c0;

  font-size: 200px;
  font-weight: 700;
  transform: 0.5s;
}
.section2 .mas:hover {
  filter: brightness(1.2);
}


/* likes */


.container-likes {
  display: flex;
  justify-content: center;
  align-items: center;

  cursor: pointer;
  background-color: #c78c65b0;
  border: none;
  border-radius: 20px;
  width: 5.5rem;
  height: 50px;
  margin: 1.2rem 2.4rem;
  transition: 0.5s;
  position: absolute;
  display: flex;
  opacity: 0.7;
  justify-content: center;
  align-items: center;
  padding: 0 1.3rem 0 0;
  border: 3px solid #746662c0;
  box-shadow: 0 0 5px rgba(138, 120, 108, 0.664);
  overflow: hidden;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.btn-active {
  opacity: 1;
  background-color: #7f9cc9c0;
}

.container-likes:hover {
  filter: brightness(1.2);
}

.btn-like {
  background-color: #503d253c;
  border: none;
  width: auto;
  height: 50px;
  padding-left: 30%;
  transition: 0.5s;
}

.likes-count {
  color: #f0d6d0b0;
  font-size: 20px;
  font-weight: 700;
  margin: 0 auto;
  margin-top: 10px;
}

.x {
  position: absolute;
  border: 3px solid #37110692;
  right: -.5rem;
  top: -0.5rem;
  font-weight: 900;
  color: rgb(28, 5, 5);
  font-size: 25px;
  padding: 0.3rem 1.2rem;
}

.empty-message {
  padding: 10rem;
  color: #30303086;
  font-size: 5rem;
  font-weight: 700;
  margin: 0 auto;
  margin-top: 10px;
  transition: 0.8s;
  border-radius: 3rem;
}

.empty-message:hover {
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.04);
  color: rgba(0, 0, 0, 0.734);
  background-color: rgba(0, 0, 0, 0.04);
}

.login {
  margin-top: 4vh;
}

/* comments */

.comments-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5%;

}

.comments-container .comment{
  display: flex;
  flex-direction: row;
  margin: 1rem 0;
}

.add-comment {
  height: 20vh;
  min-width: 60vw;
  max-width: 100vw;
  display: flex;
  flex-direction: row;
  padding: 0 2rem ;
  border-radius: 3rem;
  justify-content: start;
  background-color: #75146139;
  align-items: center;
  margin-top: 5%;
  margin-bottom: 4%;

}

.add-comment img{
  width: auto;
  height: 75%;
  border-radius: 1rem;
  margin-right: 1rem;
}


.add-comment .comment-content
{
  border: none;
  background-color: #75146139;
  color: #fff;
  font-size: 1.5rem;
  font-weight: 700;
  width: 55vw;
  
  height: 6rem;
  border-radius: 1rem;
  padding: 0 1rem;
}

@media only screen and (max-width: 1350px) {

  .section1 {
    height: 72vh;
  }

  .section1 .info-container .description {
    width: 90vw;
  }

  .section2 {
    width: 100vw;
    height: 70vh;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.846);

    max-height: auto;
  }

  .section2 .anecdotes-lenght {
    margin-top: 3%;
    margin-bottom: 3%;
  }
}

@media only screen and (max-width: 600px) {

  /* comments */

  .add-comment {
    height: 15vh;
    width: 100vw;
    padding: 0 .8rem .15rem ;
    border-radius: 1rem;
    margin-top: 5%;
    margin-bottom: 10%;
  
  }
  
  .add-comment img{
    width: auto;
    height: 70%;
    border-radius: 1rem;
    margin-right: 1rem;
  }
  
  
  .add-comment .comment-content
  {

    font-size: 1rem;
    font-weight: 700;
    width: 67vw;
    height: 3.5rem;
    border-radius: 1rem;
    padding: 0 1rem;
  }
  

  /* others */

  .login {
    margin-top: -2%;
  }

  .createForm {
    margin: 2.5vh 0 0 0;
  }

  .x {
    position: absolute;
    border: 3px solid #37110692;
    right: -0.4rem;
    top: -0.5rem;
    font-weight: 900;
    color: rgb(28, 5, 5);
    font-size: 25px;
    padding: 0.3rem 1.2rem;
  }

  .posts-container {
    margin-top: 3vh;
  }

  .post {
    width: 100vw;
    margin: 0;
    padding: 10% 6% 10%;

  }

  .post .content {
    margin-top: 25px;
    font-size: 17px;
  }

  .edit-post{
    margin-top: 2.5vh;
  }

  .title {
    font-size: 35px;
    margin-bottom: 40px;
  }



  .perfil {
    margin-top: 8%;
  }

  .section1 {
    margin-bottom: -120px;
  }

  .section1 .img-container {
    height: 30%;
    width: 50%;
  }

  .section1 .info-container .username {
    font-size: 35px;
  }

  .section1 .info-container .description {
    font-size: 20px;
    padding: 20px;
    margin-top: 20px;
  }

  .section2 .anecdotes-lenght {
    margin-top: 4%;
    margin-bottom: 1%;
  }

  /* Perfil */

  .section1 .info-container .description {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    width: 90vw;

    transition: .3s;
    font-size: 1.2rem;
  }

  @media only screen and (max-height: 600px) {

    .section1 .img-container {
    height: 30%;
    width: 30%;
    margin-top: -1.5rem;
  }

  .section2{
    display: none;
  }
}
}
